import React, { Component } from 'react';
import { Cell, Grid } from 'react-mdl';

class Landing extends Component {
    render() {
        return(
            <div style={{width:'100%', margin:'auto'}}>
                <Grid className="landing-grid">
                    <Cell col={12}>
                        <img 
                            src={process.env.PUBLIC_URL + '/img/HaznanR.jpg'}
                            alt="avatar"                                                  
                        />

                        <div className="banner-text">
                            <h1>Junior Web Developer</h1>

                            <hr/>

                            <p>HTML/CSS | PHP | JavaScript | ReactJS | CodeIgniter | Bootstrap | ReactNative</p>
                        
                            <div className="social-links">

                                {/* Linkedin */}
                                <a href="linkedin.com/in/haznanr" rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-linkedin-square" aria-hidden="true" />
                                </a>

                                {/* Github */}
                                <a href="https://github.com/hazuboi" rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-github-square" aria-hidden="true" />
                                </a>

                                {/* Twitter */}
                                <a href="https://twitter.com/hznram" rel="noopener noreferrer" target="_blank">
                                    <i className="fa fa-twitter-square" aria-hidden="true" />
                                </a>
                            </div>
                        </div>
                    </Cell>
                </Grid>
            </div>    
        )
    }
}

export default Landing;